@import "../../global.scss";


.portfolio {
    background-color: lightskyblue;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;

        @include mobile {
            font-size: 20px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile {
            flex-wrap: wrap;
        }

        li{
            font-size: 20px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $mainColor;
                color: white
            }
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            color: white;
            position: relative;
            transition: all .5 ease;
            cursor: pointer;



            h3{
                position: absolute;
                font-size: 20px;
            }


            img{
                width: 100%;
                height:100%;
                object-fit: cover;
                z-index: 1;
            }


            &:hover{
                background-color: $mainColor;
                img {
                    opacity: 0.2;
                    z-index: 0;

                }
            }
        }

    }
}